import tuiIcon from "@/components/icon/icon"
import tuiTag from "@/components/tag/tag"
import tuiLoadmore from "@/components/loadmore/loadmore"
import tuiNomore from "@/components/nomore/nomore"
import componentItem from '@/common/model/components/index';
import goods from '@/common/model/goods/index';
import bottomMenus from '@/common/model/bottom-menus';
import tuiScrollTop from "@/components/scroll-top/scroll-top"

export default {
	components: {
		tuiIcon,
		tuiTag,
		tuiLoadmore,
		tuiNomore,
		componentItem,
		goods,
		bottomMenus,
		tuiScrollTop,
	},
	data() {
		return {
			ipage:1,
			current: 0,
			scrollTop: 0,
			tabbar: [
				{icon: "home",text: "首页",size: 21}, 
				{icon: "category",text: "分类",size: 24}, 
				{icon: "cart",text: "购物车",size: 22}, 
				{icon: "people",text: "我的",size: 24},
			],
			hotSearch: [
				"休闲零食",
				"自热火锅",
				"小冰箱迷你"
			],
			components:[],
			items:[],
			query_goods_url:'',
			page:{
				ipage:0,
				hasGoods:1,
			},
			banner: [
				"1.jpg",
				"2.jpg",
				"3.jpg",
				"4.jpg",
				"5.jpg"
			],
			category: [{
				img: "1.jpg",
				name: "短袖T恤"
			}, {
				img: "2.jpg",
				name: "足球"
			}, {
				img: "3.jpg",
				name: "运动鞋"
			}, {
				img: "4.png",
				name: "中老年"
			}, {
				img: "5.png",
				name: "甜美风"
			}, {
				img: "6.jpg",
				name: "鱼尾裙"
			}, {
				img: "7.jpg",
				name: "相机配件"
			}, {
				img: "8.jpg",
				name: "护肤套装"
			}, {
				img: "9.jpg",
				name: "单肩包"
			}, {
				img: "10.jpg",
				name: "卫衣"
			}],
			newProduct: [{
				name: "时尚舒适公主裙高街修身长裙",
				present: 198,
				original: 298,
				pic: "1.jpg",
				type: 1,
				isLabel: true
			}, {
				name: "高街修身雪纺衫",
				present: 398,
				original: 598,
				pic: "2.jpg",
				type: 2,
				isLabel: true
			}, {
				name: "轻奢商务上衣",
				present: 99,
				original: 199,
				pic: "3.jpg",
				type: 1,
				isLabel: true
			}, {
				name: "品质牛皮婚鞋牛皮婚鞋品质就是好",
				present: 99,
				original: 199,
				pic: "5.jpg",
				type: 1,
				isLabel: true
			}, {
				name: "轻奢时尚大包限时新品推荐",
				present: 99,
				original: 199,
				pic: "6.jpg",
				type: 1,
				isLabel: false
			}, {
				name: "高街修身长裙",
				present: 999,
				original: 1299,
				pic: "4.jpg",
				type: 2,
				isLabel: true
			}],
			loadding: false,
			pullUpOn: true
		}
	},
	onLoad() {
		this.query();
	},
	onReachBottom(){
		this.queryItems();
	},
	methods: {
		tabbarSwitch: function(e) {
			let index = e.currentTarget.dataset.index;
			this.current = index;
			if (index != 0) {
				if (index == 1) {
					this.classify();
				} else {
					this.tui.toast("功能开发中~")
				}
			}
		},
		detail: function() {
			uni.navigateTo({
				url: '../productDetail/productDetail'
			})
		},
		classify: function() {
			uni.navigateTo({
				url: '/pages/category/category'
			})

		},
		more: function(e) {
			let key = e.currentTarget.dataset.key || "";
			uni.navigateTo({
				url: '/pages/search/search?q=' + key
			})

		},
		search: function() {
			uni.navigateTo({
				url: '/pages/search/search'
			})
		},
		queryItems() {
			console.log('url:'+this.query_goods_url);
			if(!this.query_goods_url||this.page.hasGoods==0){
				return;
			}
			this.page.ipage = this.page.ipage+1;
			this.$http.post(this.query_goods_url, {ipage:this.page.ipage}).then(res => {
				if(res.data.items&&res.data.items){
					this.items.push(
					...res.data.items
					);
				}else{
					this.page.hasGoods = 0;
				}
				this.loadding = false
			}).catch(err => {});
		},
		query() {
			this.$http.post('/app/page/home', {page_id:'youdanhui'}).then(res => {
				if(res.data.components&&res.data.components){
					this.components.push(
					...res.data.components
					);
				}
				if(res.data.items&&res.data.items.url){
					this.query_goods_url = res.data.items.url;
					this.queryItems();
				}
				uni.stopPullDownRefresh()
			}).catch(err => {});
		},
	},
	onPullDownRefresh: function() {
		this.ipage = 1;
		this.pullUpOn = true;
		this.loadding = false
	},
	onReachBottom: function() {
		if (!this.pullUpOn) return;
		this.loadding = true;
		this.queryItems();
	},
	onPageScroll(e) {
		this.scrollTop = e.scrollTop
	}
}
